const Label = styled.label({
  color: theme.colors.grey_400,
})

const copyToClipboard = str => {
  const el = document.createElement('textarea')
  el.value = str
  document.body.appendChild(el)
  el.select()
  document.execCommand('copy')
  document.body.removeChild(el)
}

const formatCode = (value, type) => {
  switch (type) {
    default:
    case 'prettified':
      return JSON.stringify(value, null, 2)
    case 'minified':
      return JSON.stringify(value)
  }
}

const FaqCard = styled.div({
  padding: rem(20),
  background: '#f9f9f9',
  borderRadius: 6,
  marginBottom: rem(20),
  border: '1px solid #d9d9d9',
})

const generateFaqSchema = faqs => ({
  '@context': 'https://schema.org',
  '@type': 'FAQPage',
  mainEntity: faqs.map(({ q, a }) => ({
    '@type': 'Question',
    name: q,
    acceptedAnswer: { '@type': 'Answer', text: a },
  })),
})

const Form = () => {
  const lastItem = useRef()
  const [faqs, setFaqs] = useState([{ q: '', a: '' }])
  const [output, setOutput] = useState('')
  const [outputType, setOutputType] = useState('minified')
  const formattedOutput = output ? formatCode(output, outputType) : ''

  return (
    <div
      css={{
        display: 'grid',
        gridTemplateColumns: '1fr',
        gridGap: rem(20),
        padding: rem(20),
        [media.tabletLg]: {
          gridTemplateColumns: '1fr 1fr',
          gridGap: rem(50),
          padding: rem(50),
        },
        background: '#fff',
        borderRadius: 8,
        boxShadow: '0 5px 30px #CBD6D9',
      }}
    >
      <div>
        <Label>FAQs</Label>
        <Space y={10} />
        {faqs.map((faq, index) => (
          <FaqCard key={index} style={{ marginBottom: 10 }}>
            <TextArea
              ref={index === faqs.length - 1 ? lastItem : null}
              placeholder="Question"
              autosize={{ minRows: 1, maxRows: 3 }}
              value={faq.q}
              onChange={e => {
                const data = faqs.map((faq, _index) =>
                  index === _index ? { ...faqs[index], q: e.target.value } : faq
                )
                setFaqs(data)
              }}
            />
            <Space y={10} />
            <TextArea
              placeholder="Answer"
              autosize={{ minRows: 2, maxRows: 6 }}
              value={faq.a}
              onChange={e => {
                const data = faqs.map((faq, _index) =>
                  index === _index ? { ...faqs[index], a: e.target.value } : faq
                )
                setFaqs(data)
              }}
            />
          </FaqCard>
        ))}
        <Button
          css={{ display: 'block', width: '100%' }}
          onClick={() => {
            setFaqs([...faqs, { q: '', a: '' }])
            setTimeout(() => lastItem.current.focus())
          }}
        >
          + Add Q/A
        </Button>
        <Space y={25} />
        <Button
          type="primary"
          size="large"
          onClick={() => setOutput(generateFaqSchema(faqs))}
        >
          Generate JSON-LD FAQ Schema
        </Button>
      </div>
      <div>
        <Label htmlFor="output-code">JSON-LD FAQ Schema Code</Label>
        <Space y={10} />
        <TextArea
          id="output-code"
          autosize={{ minRows: 8 }}
          value={formattedOutput}
          disabled={!output}
          css={{ '&.ant-input': { height: 178 } }}
        />
        <Space y={25} />
        <div
          css={{
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'space-between',
            alignItems: 'center',
            marginTop: -20,
            marginLeft: -20,
            '> *': {
              marginTop: 20,
              marginLeft: 20,
            },
          }}
        >
          <Button
            type="primary"
            size="large"
            disabled={!output}
            onClick={() => {
              copyToClipboard(formattedOutput)
              message.success('Copied to clipboard!')
            }}
          >
            Copy FAQ Schema
          </Button>
          <div css={{ color: theme.colors.grey_200 }}>
            <Radio.Group
              value={outputType}
              onChange={e => setOutputType(e.target.value)}
              size="large"
              disabled={!output}
            >
              <Radio value="minified">Minified</Radio>
              <Radio value="prettified">Prettified</Radio>
            </Radio.Group>
            <a
              href="#should-i-choose-prettified-or-minified-version"
              title="Should I choose Prettified OR Minified version?"
            >
              (What is this?)
            </a>
          </div>
          <form
            method="post"
            target="_blank"
            action="https://search.google.com/structured-data/testing-tool"
          >
            <Button type="default" size="large" htmlType="submit">
              Test in SDTT
            </Button>
            <textarea
              name="code"
              value={formattedOutput}
              css={{ display: 'none' }}
            />
          </form>
        </div>
      </div>
    </div>
  )
}

